import React, { Component } from 'react';

import item1 from './img/Our services/services_1.png';
import item2 from './img/Our services/services_2.png';
import item3 from './img/Our services/services_3.png';
import item4 from './img/Our services/services_4.png';
import item5 from './img/Our services/services_5.png';

class Services extends Component {
    state = {  }
    render() { 
        return ( 
            <div className="services">
                <h1>Our Services</h1>
                <div className="items">
                    <Item img={item1} name="Restaurants And Cafes" num="1" />
                    <Item img={item2} name="Medical & dental offices" num="2" />
                    <Item img={item3} name="Residential & Custom Homes" num="3" />
                    <Item img={item4} name="Constructions Management" num="4" />
                    <Item img={item5} name="General Contracting" num="5" />
                </div>
            </div>
        );
    }
}

class Item extends Component {
    state = {  }
    componentDidMount = () => {
        document.getElementById("Item"+this.props.num).style.backgroundImage = `url(${this.props.img})`;
    }
    render() { 
        return ( 
            <div className="item" id={"Item"+this.props.num}>
                <p>{this.props.name}</p>
                <div className="over-blue"></div>
            </div>
        );
    }
}



export default Services;