import React, { Component } from 'react';
import Logo from './img/LOGO.png'

let mor = true;

class Header extends Component {
    state = {  }
    componentDidMount = () => {
        document.getElementById("Logo-header").style.backgroundImage = `url(${Logo})`;

        if( window.innerWidth > 776 ) {
            document.getElementById("Btns").style.display = `flex`;
        } else {
            document.getElementById("Btns").style.display = `none`;
        }
    }
    more = () => {
        if (mor) {
            document.getElementById("Btns").style.display = `flex`;
            mor = false;
        } else {
            document.getElementById("Btns").style.display = `none`;
            mor = true;
        }
        
    }
    // componentDidUpdate = () => {
    //     if( window.innerWidth > 776 ) {
    //         document.getElementById("Btns").style.display = `flex`;
    //     }
    // }
    render() { 
        return ( 
            <div className="header">
                <div className="logo-header" id="Logo-header">
                    
                </div>
                <div className="btns" id="Btns">
                    <ul className="ul-header">
                        <li>HOME</li>
                        <li>ABOUT US</li>
                        <li>OUR SERVICES</li>
                        <li>OUR WORK</li>
                        <li>TESTIMONIALS</li>
                        <li>CONTACT US</li>
                    </ul>
                </div>
                <div className="more" onClick={this.more}>
                    GGG
                </div>
            </div>
        );
    }
}

export default Header;