import React, { Component } from 'react';
import land from './img/landing2.png';

class Landing extends Component {
    state = {  }
    componentDidMount = () => {
        document.getElementById("Landing").style.backgroundImage = `url(${land})` ;
    }
    render() { 
        return ( 
            <div className="landing" id="Landing">
                <div className="content-landing">
                    <div className="talk">
                        <div>  
                            <div className="bigfont">GREATCO</div>
                            <div className="bigfont">Development</div>
                            <div className="bigfont">group</div>
                        </div>
                        <div className="smallfont">For Dental and all construction needs</div>
                    </div>
                    <div className="btn1">WHAT WE DO</div>
                </div>
            </div>
        );
    }
}

//landing img --done
//header --done

export default Landing;